<template>
  	<div class="seller" ref="se">
        <div class="content">
            <div class="header">
              <div class="top">
                <div class="name">{{seller.name}}</div>
                <div class="score">
                  <star class="star"  :score="seller.score"></star>
                  <div class="num">月售{{seller.sellCount}}单</div>
                </div>
              </div>
              <div class="next">
                <div>
                  <p>起送价</p>
                  <span>{{seller.minPrice}}元</span>
                </div>
                <div>
                  <p>商家配送</p>
                  <span>{{seller.deliveryPrice}}元</span>
                </div>
                <div class="time">
                  <p>平均配送时间</p>
                  <span>{{seller.deliveryTime}}分钟</span>
                </div>
              </div>
            </div>
            <div class="slice"></div>
            <div class="notice">
              <div class="title">公告与活动</div>
              <div class="text">{{seller.bulletin}}</div>
            </div>
            <div class="supports"> 
              <div v-for="(v,i) in seller.supports" :key="i" class="discount">
                  <div class="pic">
                      <img src="./decrease.png" v-if="v.type==0">
                      <img src="./discount.png" v-if="v.type==1">
                      <img src="./discount.png" v-if="v.type==2">
                      <img src="./invoice.png" v-if="v.type==3">
                      <img src="./guarantee.png" v-if="v.type==4">                          
                  </div>
                  <div class="info">{{v.description}}</div> 
              </div>
            </div>
            <div class="slice"></div>            
            <div class="envi">
              <div class="title">商家实景</div>
              <div class="swiper-container">
                <div class="swiper-wrapper">
                   <div class="swiper-slide" v-for="(v,i) in seller.pics" :key="i">
                      <img :src="v">
                   </div>
                   <div class="swiper-slide"></div>
                </div>
            </div>
            <div class="clear"></div>
            </div>
            <div class="slice"></div>            
            <div class="last">
              <div class="title">商家信息</div>
              <div class="detail" v-for="(info,i) in seller.infos" :key="i">{{info}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters ,mapActions} from 'vuex'
import star from '../star/star'
import BScroll from 'better-scroll'
export default {
  components:{
    star
  },
  computed:{
    ...mapGetters([
      'seller'
    ]),
    
  },
  created(){
    this.$store.dispatch('getSeller')
  },
  watch:{
      seller(){
        this.$nextTick( ()=>{ 
          if( !this.seBScroll ){
            this.seBScroll = new BScroll( this.$refs.se , {click:true} );
          }else{
            this.seBScroll.refresh();
          }
          setTimeout( () => {
            
            this.swiper();
          },300)
        })
    },
  },
  methods:{
	  ...mapActions([

    ]),
    swiper(){
        var swiper = new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        slidesPerView: 3,
        paginationClickable: true,
        spaceBetween: 30,
        freeMode: true
    });
    }
  }
}
</script>

<style scoped lang='less'>
  .seller{
    position: absolute;
    top: 3.5rem;
    bottom:0.94rem;
    width: 100%;
    height:8.9rem;
    overflow: hidden;
    .header{
      padding: 0.36rem;
      border-bottom: 1px solid rgba(7, 17, 27,0.1);
      .top{
        padding-bottom: 0.36rem;
        border-bottom: 1px solid rgba(7, 17, 27,0.1);
        .name{
          font-size: 0.28rem;
          color:rgb(7, 17, 27);
          line-height: 0.28rem;
        }
        .score{
          margin-top: 0.16rem;
          .star,.num{
            display: inline-block;
          }
          .num{
            font-size: 0.20rem;
            color:rgb(77, 85, 93);
            line-height: 0.36rem;
            margin-left: 0.24rem;
            vertical-align: top;
          }
        }
      }
      .next{
        display: flex;
        div{
          flex:1;
          text-align: center;
          margin-top: 0.36rem;
          border-right: 1px solid rgba(7, 17, 27,0.1);
          p{
            font-size: 0.2rem;
            color:rgb(147, 153, 159);
            line-height: 0.2rem;
            margin-bottom: 0.08rem;
          }
          span{
            font-size: 0.48rem;
            font-weight: 200;
            color:rgb(7, 17, 27);
            line-height: 0.48rem;
          }
        }
        .time{
          border-right: 0;
        }

      }
    }
    .slice{
      height: 0.36rem;
      width: 100%;
      background-color: rgba(77, 85, 93,0.1); 
      border-bottom: 1px solid rgba(7, 17, 27,0.1);
    }
    .notice{
      padding: 0.36rem;
      padding-bottom: 0;
      .title{
        font-size: 0.28rem;
        color:rgb(7, 17, 27);
        line-height: 0.28rem;
      }
      .text{
        padding-left: 0.24rem;
        padding-top: 0.16rem;
        font-size: 0.24rem;
        font-weight: 200;
        color:rgb(240, 20, 20);
        line-height: 0.48rem;
        padding-bottom: 0.32rem;
        border-bottom: 1px solid rgba(7, 17, 27,0.1);         
      }
    }
    .supports{
      padding:0 0.36rem;
      .discount{
          padding: 0.32rem 0.24rem;
          height:0.4rem;
          line-height:0.4rem;
          border-bottom: 1px solid rgba(7, 17, 27,0.1); 
          div{
              float:left;
              margin-right:0.12rem;
              font-size:0.24rem;
              font-weight:200;
              color:rgb(7,17,27);
              img{
                  height:0.4rem;
                  width:0.4rem;
              }
          } 
      }
    }
    .envi{
      padding: 0.36rem;
      .clear{
        clear: both;
      }
      .title{
        font-size: 0.28rem;
        color:rgb(7, 17, 27);
        line-height: 0.28rem;
        padding-bottom: 0.14rem;
      }
      .swiper-container {
        width: 100%;
        height: 1.8rem;
        margin: 0.2rem auto;
        .swiper-slide {
          text-align: center;
          background: #fff;
          width: 2.4rem;
          height: 100%;
          margin-left: 0.12rem;
          img{
            height: 100%;
            width: 2.4rem;;
          }
        }
      }
    }
    .last{
      padding: 0.36rem;
      .title{
        font-size: 0.28rem;
        color:rgb(7, 17, 27);
        line-height: 0.28rem;
      }
      .detail{
        padding: 0.32rem 0.24rem;
        font-size: 0.24rem;
        font-weight: 200;
        color:rgb(7, 17, 27);
        line-height: 0.32rem;
        border-bottom: 1px solid rgba(7, 17, 27,0.1);
      }
    }
  }
	
</style>



