<template>
  <div class="sicon">
        <div class="minus" @click = 'reduce' v-show="food.count>0">
            <i class="icon iconfont icon-jianhao"></i>                                
        </div>
        <div class="num" v-show="food.count>0">{{food.count}}</div>
        <div class="plus" @click = 'add' >
            <i class="icon iconfont icon-jiahao"></i>
        </div>
    </div>
</template>

<script>
import Vue from 'vue';
export default {
    props:['food'],
    methods:{
        add(){
            if( !this.food.count ){
                Vue.set( this.food , 'count' , 1);
            }else{
                this.food.count ++;
            }
        },
        reduce(){
            if( this.food.count ){
                this.food.count --;
            }
        }
    }
}
</script>

<style scoped lang="less">
    .sicon{
        position: absolute;
        right:0.36rem;
        bottom:0.2rem;
        width: 1.38rem;
        div{
            float: left;
            font-size: 0.2rem;
            color:rgb(147,153,159);
            i {
                font-size: 0.48rem;
                color:rgb(0, 160, 220);
                line-height: 0.48rem;
            }
        }
        .num{
            width:0.43rem;
            text-align: center;
            padding-top:0.14rem;
        }
        .plus{
            position: absolute;
            right:0;
            bottom:0;
        }
    }
</style>










