<template>
  	<div class="goods">
        <div class="Lside" ref="ls">
            <ul>
                <li class="types"  v-for="(v,i) in goods" :key="i" @click="selectedMenu(i)" :class="{'current' : currentIndex === i}">
                    <span class="list">{{v.name}}</span>
                </li>
            </ul>
        </div>
        <div class='Rside' ref='rs'>
            <ul>
                <li class="detail food-list-hook" v-for="(v,i) in goods" :key="i" >
                    <div class="name">{{v.name}}</div>
                    <div class="foods" v-for="(food,j) in v.foods" :key="j" >
                        <div class="pic" @click="showFood(food)">
                            <img :src="food.icon" >
                        </div>
                        <div class="other">
                            <div class="fname">{{food.name}}</div>
                            <div class="fdesc" v-if="food.description">{{food.description}}</div>
                            <div class="num"> <span>月售{{food.sellCount}}份</span>  <span>好评率{{food.rating}}％</span> </div>
                            <div class="price">
                                <span>¥{{food.price}}</span>
                                <span class="old" v-if="food.oldPrice">¥{{food.oldPrice}}</span>
                            </div>
                            <Change :food = 'food'></Change>    
                        </div>
                        <div class="c"></div>
                    </div>
                </li>
            </ul>
        </div>
        <div>
            <food></food>
        </div>
    </div>
</template>

<script>
import { mapGetters ,mapActions} from 'vuex'
import Change from './change'
import BScroll from 'better-scroll'
import food from '../food/food'
export default {
    components:{
        Change,
        food
    },
    data(){
        return {
            listHeight:[],
            scrolly:0
        }
    },
  created(){
        this.$store.dispatch('getGoods');
        
  },
  watch:{
      goods(){
          this.$nextTick( ()=>{
              if (!this.rBScroll) {
                this.rBScroll = new BScroll( this.$refs.rs , {click:true,probeType: 3,} );                      
                this.rBScroll.on('scroll', (pos) => {
                    this.scrolly = Math.abs(Math.round(pos.y))+2;
                });
                this.lBScroll = new BScroll( this.$refs.ls , {click:true} );
              }else{
                  this.rBScroll.refresh();                  
              }
              
              this.calcHeight();
          })
      },
  },
  computed:{
    ...mapGetters([
        'goods',
    ]),
    currentIndex() {
        for (let i = 0; i < this.listHeight.length; i++) {
            let height = this.listHeight[i];
            let height2 = this.listHeight[i + 1];
            if ((this.scrolly >= height && this.scrolly < height2)) {
                return i;
            }
        }
        return 0;
    },
    
      
  },
  methods:{
    ...mapActions([
    ]),
    calcHeight(){
        let foodList =  this.$refs.rs.getElementsByClassName("food-list-hook");
        let height = 0;
        this.listHeight.push(height);
        for (var i = 0; i < foodList.length; i++) {
            height +=foodList[i].clientHeight;
            this.listHeight.push(height);
        }
    },
    selectedMenu(i){
        let foodList = this.$refs.rs.getElementsByClassName('food-list-hook');
        let el = foodList[i];
        this.rBScroll.scrollToElement(el,300);
    },
    showFood(food){
        this.$store.dispatch('showFood',food)
    }
  }
}
</script>

<style scoped lang='less'>
    .goods{
        position: absolute;
        top: 3.5rem;
        bottom:0.98rem;
        display: flex;
        width: 100%;
        height:8.9rem;
        overflow: hidden;
        .Lside{
            height: 8.9rem;
            flex:0 0 1.6rem;
            width: 1.6rem;
            background-color: #f3f5f7;
            .types{
                width: 1.12rem;
                padding:0 0.24rem;
                height: 1.08rem;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                display: table;
                color:black;
                font-size:0.24rem;
                font-weight: 200;
                .list{
                    display: table-cell;
                    vertical-align: middle; 
                    text-align: center;
                    width:100%;
                    word-wrap:break-word;
                }
                &.current{
                    background-color: white;
                    color:rgb(26,161,217);
                    font-size: 0.26rem;
                }
            }
        }
        .Rside{
            height:8.9rem;            
            flex:1;
            .detail{
                width: 100%;
                .name{
                    width: 100%;
                    height: 0.65rem;
                    font-size: 0.26rem;
                    color:#ccc;
                    line-height: 0.65rem;
                    padding-left: 0.24rem;
                    border-left: 2px solid rgb(217, 221, 225);
                    background-color: #f3f5f7;
                }
                .foods{
                    padding: 0.36rem;
                    height: 1.5rem;
                    border-bottom: 1px solid #ccc;
                    position: relative;
                    div{
                        margin-bottom: 0.16rem;
                    }
                    .pic{
                        float:left;                        
                        width: 1.14rem;
                        height: 1.14rem;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .other{
                        margin-left: 0.2rem;
                        float:left;
                        width: 3.7rem;
                        margin-bottom: 0;                        
                        .fname{
                            font-size: 0.28rem;
                            color:rgb(7, 17, 27);
                            line-height: 0.28rem;
                        }
                        .fdesc,.num{
                            font-size: 0.2rem;
                            color:rgb(147, 153, 159);
                            line-height: 0.2rem;
                            width: 100%;
                            span{
                                display:inline-block;
                                margin-right: 0.24rem;
                            }
                        }
                        .price{
                            font-size: 0.28rem;
                            color:red;
                            line-height: 0.48rem;
                            margin-bottom: 0;
                            span{
                                display: inline-block;
                            }
                            .old{
                                margin-left: 0.16rem;
                                font-size: 0.2rem;
                                color:rgb(147, 153, 159);
                                text-decoration:line-through;
                            }
                        }
                    
                    }
                    .c{
                        clear: both;
                    }
                }
            }
        } 
    }
	
</style>



