<template>
    <transition enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
        <div class="food" v-show="foodShow"  ref='food'>
            <div class="content">
                <div class="hImg">
                    <i class="icon iconfont icon-shiliangzhinengduixiang4" @click="hideFood"></i>
                    <img :src="food.image">
                </div>
                <div class='detail'>
                    <div class="fname">{{food.name}}</div>
                    <div class="fdesc" v-if="food.description">{{food.description}}</div>
                    <div class="num"> <span>月售{{food.sellCount}}份</span>  <span>好评率{{food.rating}}％</span> </div>
                    <div class="price">
                        <span>¥{{food.price}}</span>
                        <span class="old" v-if="food.oldPrice">¥{{food.oldPrice}}</span>
                    </div>
                    <div class="buy" v-show='!food.count' @click = 'add'>
                        加入购物车
                    </div>
                 <Change class="change" :food = 'food' v-show='food.count'></Change>    
                </div>
                <div class="intro">
                    <div class="title">商家介绍</div>
                    <div class="info" v-if="food.info">{{food.info}}</div>
                </div>
                <div class="ratings">
                    <selectrating :flag='1'></selectrating>
                    <div class="list">
                        <ul>
                            <li v-for="(rating,i) in selectRatings" :key="i">
                                <div class="every">
                                    <div class="time">{{rating.rateTime | formatDateTime}}</div>
                                    <div class="text">
                                        <i class="icon iconfont icon-damuzhion" v-if="rating.rateType==0"></i>
                                        <i class="icon iconfont icon-down" v-if="rating.rateType==1"></i>
                                        {{rating.text}}
                                    </div>
                                    <div class="user">
                                        <span>{{rating.username}}</span>
                                        <div class="headImg">
                                            <img :src="rating.avatar" alt="">
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import animate from 'animate.css'
import Change from '../goods/change'
import select from '../ratingselect/ratingselect'
import Vue from 'vue'
export default {
    components:{
        Change,
        'selectrating':select
    },
    computed:{
        ...mapGetters([
        'foodShow',
        'food',
        'selectRatings'
        ]),
    },
    filters:{
        formatDateTime(t){
            var theDate = new Date(t);
            var _hour = theDate.getHours();
            var _minute = theDate.getMinutes();
            var _second = theDate.getSeconds();
            var _year = theDate.getFullYear()
            var _month = theDate.getMonth();
            var _date = theDate.getDate();
            if(_hour<10){_hour="0"+_hour }
            if(_minute<10){_minute="0"+_minute }
            if(_second<10){_second="0"+_second  }
            _month = _month + 1;
            if(_month < 10){_month = "0" + _month;}
            if(_date<10){_date="0"+_date  }
            return  _year + "-" + _month + "-" + _date + " " + _hour + ":" + _minute + ":" + _second ;
        }
    },
    watch:{
      food(){
          this.$nextTick( ()=>{
              if(!this.foodBScroll){
                this.foodBScroll = new BScroll( this.$refs.food , {click:true} );
              }else{
                this.foodBScroll.refresh();
              } 
          })
      },
      selectRatings(){
         this.$nextTick( ()=>{
              if(!this.foodBScroll){
                this.foodBScroll = new BScroll( this.$refs.food , {click:true} );
              }else{
                this.foodBScroll.refresh();
              } 
          })
      }
    },
    methods:{
        hideFood(){
          this.$store.dispatch('hideFood');
          this.$store.dispatch('select',2);
          this.$store.dispatch('selectRatings');
        },
        add(){
			Vue.set( this.food , 'count' , 1 );
        },
        
    }
    
}

</script>

<style scoped lang="less">
    .food{
        position: fixed;
		top: 0;
		left: 0;
		right: 0;
        bottom: 0.98rem; 
        background-color:rgb(243,245 , 247);
        overflow: hidden;
        .hImg{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
            }
            i{
                font-size: 0.48rem;
                color: rgba(220, 220, 220, 0.6);
                position: absolute;
                top:0.5rem;
                left:0.4rem;
                z-index: 99;
            }
        }
        .detail{
            padding: 0.36rem;
            margin-bottom: 0.32rem;   
            background-color: white; 
            position: relative;                    
            .fname{
                font-size: 0.28rem;
                color:rgb(7, 17, 27);
                line-height: 0.28rem;
                font-weight: 700;
                margin-bottom: 0.16rem;
            }
            .fdesc,.num{
                font-size: 0.2rem;
                color:rgb(147, 153, 159);
                line-height: 0.2rem;
                width: 100%;
                margin-bottom: 0.36rem;
                span{
                    display:inline-block;
                    margin-right: 0.24rem;
                }
            }
            .price{
                font-size: 0.28rem;
                color:red;
                line-height: 0.48rem;
                margin-bottom: 0;
                span{
                    display: inline-block;
                }
                .old{
                    margin-left: 0.16rem;
                    font-size: 0.2rem;
                    color:rgb(147, 153, 159);
                    text-decoration:line-through;
                }
            }
            .buy{
                position: absolute;
                bottom: 0.36rem;
                right:0.36rem;
                font-size: 0.2rem;
                color:rgb(255, 255, 255);
                line-height: 0.24rem;
                background-color: rgb(0, 160, 220);
                border-radius: 0.24rem;
                height: 0.24rem;
                width:1.1rem;
                padding: 0.12rem 0.24rem;
            }
            .change{
                bottom:0.36rem;
                width:1.4rem;
            }
        }
        .intro{
            padding: 0.36rem;
            background-color: white;
            margin-bottom: 0.32rem;
            .title{
                font-size: 0.28rem;
                color:black;
                font-weight: 700;
                margin-bottom: 0.12rem;
            }
            .info{
                height:1.4rem;
                font-size: 0.24rem;
                color:rgb(77, 85, 93);
                font-weight: 200;
                line-height: 0.48rem;
                padding: 0 0.16rem;
            }
        }
        .ratings{
            .header{
                padding: 0.36rem;
                padding-bottom: 0.24rem;
                background-color: white;
                margin-bottom: 0.02rem;
                .title{
                    font-size: 0.28rem;
                    color:black;
                    font-weight: 700;
                    margin-bottom: 0.12rem;
                } 
                .select{
                    padding-bottom: 0.36rem;
                    border-bottom: 1px solid #ccc;
                    div{
                        display: inline-block;
                        margin-right: 0.16rem;
                        padding: 0.16rem 0.24rem;
                        font-size:0 .24rem;
                        line-height: 0.32rem;
                        border-radius: 0.02rem;
                        span{
                            font-size: 0.16rem;
                        }
                    }
                    .all{
                        background-color: rgb(0, 160, 220);
                        color:rgb(255, 255, 255);                        
                    }
                    .reco{
                        background-color: rgba(0, 160, 220,0.2);
                        color:rgb(77, 85, 93);                        
                    }
                    .bad{
                        color:rgb(77, 85, 93);                                                
                        background-color: rgba(77, 85, 93,0.2);
                    }
                }
                .have{
                    i{
                        font-size: 0.48rem;
                    }
                    margin-top: 0.24rem;
                    font-size: 0.24rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.48rem;
                }
            }
            .list{
                width:100%;
                background-color: white;
                .every{
                    padding: 0 0.36rem;
                    padding-top: 0.32rem;
                    padding-bottom: 0.32rem;
                    border-bottom: 1px solid rgba(7, 17, 27,0.1);
                    position: relative;
                    .time{
                        font-size: 0.2rem;
                        color:rgb(147, 153, 159);
                        line-height: 0.24rem;
                        margin-bottom: 0.12rem;
                    }
                    .text{
                        font-size: 0.24rem;
                        color:rgb(7, 17, 27);
                        line-height: 0.32rem;
                        i{
                           color:rgb(147, 153, 159);
                           line-height: 0.48rem;
                        }
                        .icon-damuzhion{
                            color:rgb(0, 160, 220)
                        }
                    }
                    .user{
                        position: absolute;
                        right:0.36rem;
                        top:0.32rem;
                        .headImg{
                            height: 0.24rem;
                            width: 0.24rem;
                            display: inline-block;
                            margin-left: 0.12rem;
                            img{
                                height:100%;
                                width:100%;
                                border-radius: 0.12rem;
                            }
                        }
                        span{
                            font-size: 0.2rem;
                            color:rgb(147, 153, 159);
                            line-height: 0.24rem; 
                        }
                    }
                }

            }
        }
    }
</style>










