<template>
  	<div class="header">
		<div class="content">
			<div class="logo">
				<img :src="seller.avatar" @click="showDetail">
			</div>
			<div class="detail">
				<div class="name">
					<div><img src="../../../static/imgs/brand.png" ></div>
					<div><span>{{seller.name}}</span></div>
				</div>
				<p class="description">
					<span>{{seller.description}}</span><span>/{{seller.deliveryTime}}分钟送达</span>
				</p>
				<div class="supports">
					<div><img src="../../../static/imgs/decrease.png"></div>
					<div class="desc"><span v-if="seller.supports">{{seller.supports[0].description}}</span></div>
				</div>
			</div>
			<div class="num">
				<span v-if="seller.supports">{{seller.supports.length}}个</span><i>></i>
			</div>
		</div>
		<div class="notice">
			<div class="pic"><img src="../../../static/imgs/bulletin.png" ></div>
			<div class="desc">{{seller.bulletin}}</div>
		</div>
		<div class="background">
			<img :src="seller.avatar">
		</div>
  	</div>
</template>

<script>
import { mapGetters ,mapActions} from 'vuex'
export default {
  computed:{
    ...mapGetters([
      'seller'
    ])
  },
  methods:{
	  ...mapActions([
		  'showDetail'
	  ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
	.header{
		width:100%;
		height:2.66rem;
		position :relative;
		background-color: rgba(7, 17, 27, 0.5);	
		overflow: hidden;
		.content{
			position: relative;
			width:100%;
			height:1.28rem;
			padding: 0.48rem 0.24rem 0.36rem 0.48rem;
			.logo{
				float:left;
				width: 1.28rem;
				height: 1.28rem;
				img{
					width: 100%;
					border-radius: 0.04rem;				
				}
			}
			.num{
				font-size: 0.2rem;
				color:rgb(255, 255, 255);
				font-weight: 200;
				width:0.55rem;
				height:0 .24rem;
				padding: 0.14rem 0.16rem;
				background-color: rgba(0, 0, 0, 0.2);
				position: absolute;
				right:0.87rem;
				bottom:0.36rem;
				border-radius: 0.24rem;
				span{
					margin-right: 0.04rem;
				}
			}
			.detail{
				float:left;
				padding: 0.04rem 0 0.04rem 0.32rem;
				.name{
					font-size: 0.32rem;
					color:rgb(255, 255, 255);
					font-weight: bold;
					height:0.36rem;
					line-height: 0.36rem;
					margin-bottom: 0.16rem;
					div{
						height:100%;
						float: left;
						img{
							width:0.6rem;
							height:100%;
							margin-right: 0.12rem;	
						}
					}
				}
				.description{
					font-size: 0.24rem;
					color:rgb(255, 255, 255);
					font-weight: 200;
					line-height:0 .24rem;
					margin-bottom: 0.2rem;					
				}
				.supports{
					font-size: 0.2rem;
					color:rgb(255, 255, 255);
					font-weight: 200;
					height:0.24rem;	
					line-height: 0.24rem;			
					div{
						height:100%;
						float: left;
						img{
							width:0.24rem;
							height:100%;
							margin-right: 0.08rem;	
						}
					}
				}
			}
		}
		.background{
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width :100%;
			height :100%;
			border: 1px solid red;
			filter: blur(5px);
			background-color: rgba(7, 17, 27, 0.5);
			z-index: -1;
			img{
				width: 100%;
				height: 100%;
			}
		}
		.notice{
			background-color: rgba(7, 17, 27, 0.2);
			height:0.56rem;
			padding:0  0.24rem;
			.pic{
				height: 0.26rem;
				width: 0.44rem;
				float:left;
				margin-top: 0.15rem;
				margin-right: 0.08rem;
				img{
					height:100%;
					width:100%;
				}
			};
			.desc{
				font-size: 0.2rem;
				color:rgb(255, 255, 255);
				font-weight: 200;
				height:0.56rem;
				line-height: 0.56rem;
				width:6.4rem;
				overflow: hidden;
				text-overflow:ellipsis;
				white-space:nowrap;
			}
		}
		
	}
	
</style>



