<template>
    <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
            <div class="detail" v-show="detailShow">
                <div class="bg" v-show="detailShow"> </div>
                <div  class="top">
                    <div class="name">{{seller.name}}</div>
                    <div class="star"><star :score = 'seller.score' :size = "'big'"></star></div>
                </div>
                <div class="info">
                    <div class="left"></div>
                    <div class="word">优惠信息</div>
                    <div class="right"></div>                    
                </div>
                <div class="supports"> 
                    <div v-for="(v,i) in seller.supports" :key="i" class="discount">
                        <div>
                            <!-- <img :src="imgUrl[i-1]"> -->
                            <img src="./decrease.png" v-if="v.type==0">
                            <img src="./discount.png" v-if="v.type==1">
                            <img src="./discount.png" v-if="v.type==2">
                            <img src="./invoice.png" v-if="v.type==3">
                            <img src="./guarantee.png" v-if="v.type==4">                          
                        </div>
                       <div class="info">{{v.description}}</div> 
                    </div>
                </div>
                <div class="info">
                    <div class="left"></div>
                    <div class="word">商家公告</div>
                    <div class="right"></div>                    
                </div>
                <div class="bulletin">
                   {{seller.bulletin}}                  
                </div>
                <div class='i' @click="hideDetail">
                    <i class="icon iconfont icon-X"></i>
                </div>
               
            </div> 
       
    </transition>
</template>

<script>
import { mapGetters } from 'vuex'
import animate from 'animate.css'
import star from '../star/star.vue'
export default {
  props:['name'],
  components:{
      star
  },
//   data:function(){
//      return{
//          imgUrl:['./decrease.png','./discount.png','./decrease.png','./discount.png','./decrease.png']
//      } 
//   },
  computed:{
    ...mapGetters([
      'seller',
      'detailShow'
    ])
  },
  methods:{
      hideDetail(){
          this.$store.dispatch('hideDetail')
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
    .bg{
       position: absolute;
		top: 0;
		left: 0;
		right: 0;
        bottom: 0; 
        background-color:rgba(7,17 , 27, 0.8);
        z-index:-1 ;
    }
	.detail{
        position: absolute;
        z-index: 111;
		top: 0;
		left: 0;
		right: 0;
        bottom: 0; 
        padding: 1.28rem 0.72rem 0.64rem; 
        .top{
            padding-bottom: 0.56rem;
            margin: 0 auto;
            text-align: center;
            .name{
                font-size: 0.32rem;
                font-weight: 700;
                color:rgb(255, 255, 255);
                line-height: 0.32rem;
                height:0.32rem;
                margin-bottom: 0.32rem;
            };
            .star{
                padding: 0.04rem 0;
            }
        };
        .info{
            height:0.3rem;
            div{
                float: left;
            }
        }
        .word{
            font-size: 0.26rem;
            font-weight: 700;
            color:rgb(255, 255, 255);
            line-height: 0.26rem;
            height:0.26rem;
            padding: 0 0.23rem;
            width:1.12rem;
        }
        .left,.right{
            width:2.24rem;
            height: 0.02rem;
            margin-top: 0.12rem;
            background-color: rgba(255, 255, 255, 0.2)
        }
        .supports{
            width:100%;
            padding:0.48rem 0 0.56rem 0.24rem;
            .discount{
               width:100%;
               height:0.32rem;
               line-height:0.32rem;
               margin-bottom:0.24rem;
               div{
                   float:left;
                   margin-right:0.12rem;
                   font-size:0.24rem;
                   font-weight:200;
                   color:rgb(255,255,255);
                   img{
                       height:0.32rem;
                       width:0.32rem;
                   }
               } 
            }
        }
        .bulletin{
            padding:0.48rem 0.24rem;
            font-size:0.24rem;
            font-weight:200;
            color:rgb(255,255,255);
            line-height:0.48rem;
        }
      .i{
            width:6.06rem;
            position: absolute;
            bottom:0.64rem;
            text-align: center;
            i{
                font-size: 0.64rem;
                color:rgba(255,255,255,0.5);
            }
        
        // transition: font-size 0.25s ease-out 0s
      }
    }
</style>



