import types from '../types'
import axios from 'axios'
import Vue from "Vue"
import rating from './ratings'


var state = {
    goods:[],
    foodShow:false,
    food:{},
    selectedShow:false,
    foodratings:[]
}

var getters = {
    goods(state){
        return state.goods
    },
    foodShow(state){
        return state.foodShow
    },
    food( state ){
		return state.food;
    },
    selectedShow(state){
        return state.selectedShow
    },
    foodratings(state){
        return state.foodratings        
    }
}

var actions = {
    getGoods({ commit ,state }){
        axios.get('./data.json').then( (res)=>{
            // if( res.data.error === 0 ){
                commit( types.GET_GOODS , res.data.goods );
            // }
        })
    },
    showFood({commit,state},food){
        commit(types.showFood,food)
    },
    hideFood({commit,state}){
        commit(types.hideFood)
    },
    selected({commit,state}){
        commit('selected')
    }
}

var mutations = {
    [types.GET_GOODS](state,data){
        state.goods=data
    },
    [types.showFood](state,food){
        rating.state.selectRatings=food.ratings;
        state.food=food;
        state.foodratings=food.ratings;
        state.foodShow = true;
    },
    [types.hideFood](state){
        rating.state.onlyContent=true;
        state.foodShow = false;
    },
    selected(state){
        state.selectedShow = !state.selectedShow
    }
}
export default{
    state,
    getters,
    actions,
    mutations
}






