<template>
    <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
        <div class="bg" v-show="selectedShow"> 
            <div class="detail" v-show="selectedShow">
                <div class="top">
                    <div class="car">购物车</div>
                    <div class="clear" @click="clear(selectFoods)">清空</div>
                </div>
                <div v-for="(food,i) in selectFoods" :key="i" class="list">
                    <div class="name">{{food.name}}</div>
                    <div class="price">¥{{food.count*food.price}}</div>
                    <div class="change"><Change :food = 'food'></Change></div>  
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import { mapGetters } from 'vuex'
import animate from 'animate.css'
import Change from '../goods/change'
export default {
  components:{
      Change
  },
  computed:{
    ...mapGetters([
      'seller',
      'selectedShow',
      'goods'
    ]),
    selectFoods(){
        let foods = [];
        this.goods.forEach( good => {
            good.foods.forEach( food => {
                if( food.count >0 ){
                    foods.push( food );
                }
            })
        });
        return foods;        
    },
    
  },
  methods:{
    hideDetail(){
        this.$store.dispatch('hideDetail')
    },
    clear(arr){
        for (var i = 0; i < arr.length; i++) {
            arr[i].count=0;
        }
    }
  }
  
}
</script>

<style scoped lang='less'>
    .bg{
       position: absolute;
		top: 0;
		left: 0;
		right: 0;
        bottom:0; 
        background-color:rgba(7,17 , 27, 0.6);
        z-index: 99;
    }
	.detail{
        position: absolute;
        z-index: 100;
		// top:6rem;
		left: 0;
		right: 0;
        bottom:0.96rem; 
        background-color: white;
        .top{
            padding: 0 0.36rem;
            height: 0.8rem;            
            background-color: #f3f5f7;
            border-bottom: 1px solid rgba(147, 153, 159,0.3);
            .car{
                font-size: 0.28rem;
                font-weight: 200;
                color:rgb(7,17 , 27);
                line-height: 0.8rem;
                height: 0.8rem;
                float: left;
            }
            .clear{
                line-height: 0.8rem;
                height: 0.8rem;
                float: right;
                color: rgb(0, 160, 220)
            }
        }
        .list{
            margin: 0 0.36rem;
            height:0.76rem;
            position: relative;
            border-bottom: 1px solid rgba(147, 153, 159,0.3);                     
            
            .name{
                height:0.76rem;
                line-height: 0.76rem;
                font-size: 0.28rem;
                font-weight: 700;
                color:rgb(7,17 , 27);
                float: left;
            }
            .price{
                font-size: 0.28rem;
                font-weight: 200;
                color:rgb(240,20 , 20);
                font-weight: 700;
                height:0.76rem;
                float: right;
                line-height: 0.76rem;
                margin-right: 2rem;
            }
            .change{
                width: 1.5rem;
            }
        }

      }
</style>



