<template>
    <div class="shopping">
        <div class="car" :class = "{'bg' : totalCount > 0 }" @click="selected">
            <i class="icon iconfont icon-gouwuche"></i>
        </div>
        <div class="count" v-if="totalCount">{{totalCount}}</div>
        <div class="total" :class = "{'bg' : totalCount > 0 }">¥{{totalPrice}}</div>
        <div class="cost">另需配送费¥{{seller.deliveryPrice}}元</div>
        <div class="min" :class = "{'bg' : totalPrice >=20 }" @click="goPay">{{pay}}</div>
    </div>
</template>

<script>
import { mapGetters ,mapActions} from 'vuex'
export default {
    props:[name],
  computed:{
    ...mapGetters([
        'seller',
        'goods'

    ]),
    selectFoods(){
        let foods = [];
        this.goods.forEach( good => {
            good.foods.forEach( food => {
                if( food.count >0 ){
                    foods.push( food )
                }
            })
        });
        return foods;        
    },
    totalCount(){
        let count = 0 ;
        this.selectFoods.forEach( food => {
            count += food.count;
        });
        return count;
    },
    totalPrice(){
        let price = 0;
        this.selectFoods.forEach(food => {
            price += food.count * food.price;
        })
        return price;
    },
    pay(){
        if( this.totalPrice === 0 ){
            return `¥ ${this.seller.minPrice} 元起送`;
        }else if ( this.totalPrice < this.seller.minPrice ) {
            return `还差¥ ${this.seller.minPrice - this.totalPrice } 元起送`
        }else{
            return "去结算";
        }
    }
  },
  created(){
        this.$store.dispatch('getGoods')
  },
  methods:{
      ...mapActions([
		  'selected'
	  ]),
      goPay(){
          if(this.totalPrice>=20){
             confirm('确定付款吗？') 
          }
      }
  }
}
</script>
<style scoped lang='less'>
  .shopping{
    position: absolute;
    height: 0.98rem;
    width: 100%;
    bottom:0;
    background-color: #141d27;
    z-index: 101;
    div{
        float: left;
    }
    .count{
        position: absolute;
        text-align: center;
        left:0.88rem;
        top:-0.16rem;
        width: 0.24rem;
        padding: 0 0.12rem;
        padding-top: 0.02rem;
        height:0.32rem;
        font-size: 0.18rem;
        font-weight: 700;
        color:rgb(255,255,255);
        line-height: 0.32rem;
        border-radius: 0.16rem;
        background-color: rgb(240,20,20);
        box-shadow: 0 0.04rem 0.08rem 0 rgba(0, 0, 0, 0.8);
    }
    .car{
        position: absolute;
        left: 0.24rem;
        bottom:0.04rem; 
        background-color:rgb(43, 52, 66);
        width: 0.48rem;
        height: 0.48rem;
        border: 0.12rem solid #141d27;
        border-radius: 0.56rem;
        text-align: center;
        padding:0.2rem;
        &.bg{
            background-color:rgb(0, 160, 220);
            i{
                color: white;
            }
        }
        i{
            font-size: 0.48rem;
            color:rgb(128, 133, 138);
        }
    }
    .total{
        font-size: 0.32rem;
        color:rgba(255, 255, 255, 0.4);
        font-weight: 700;
        line-height: 0.48rem;
        width: 0.9rem;
        height:0.48rem;
        margin-left: 1.6rem;
        margin-top: 0.24rem;
        margin-right: 0.24rem;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        &.bg{
            color: white;
        }
    }
    .cost{
        font-size: 0.32rem;
        color:rgba(255, 255, 255, 0.4);
        font-weight: 700;
        line-height: 0.48rem;
        margin-top: 0.24rem;
    }
    .min{
        position: absolute;
        font-size: 0.28rem;
        color:rgba(255, 255, 255, 0.4);
        font-weight: 700;
        height:0.64rem;
        line-height: 100%;
        text-align: center;
        width: 2.1rem;
        background-color: rgb(43 , 51,59);
        bottom:0;
        right:0rem;
        padding-top: 0.34rem;
        &.bg{
            background-color:rgb(0, 160, 220);
            color:white;
        }
    }
}
</style>



