
import types from '../types'
import axios from 'axios'
import good from './goods'


var state = {
    ratings:[],
    types:['推荐','不推荐','全部'],
    selectType:2,
    selectRatings:[],
    onlyContent : true
}

var getters = {
    ratings(state){
        
        return state.ratings
    },
    types(state){
        return state.types
    },
    selectType(state){
        return state.selectType        
    },
    selectRatings(state){     
        return state.selectRatings                
    },
    onlyContent(state){
        return state.onlyContent;
    }
    
}

var actions = {
    getRatings({ commit ,state }){
        axios.get('./data.json').then( (res)=>{
            // if( res.data.error === 0 ){
                commit( types.GET_RATINGS , res.data.ratings );
            // }
        })
    },
    changeTypes( {commit } , _types ){
		commit ( 'changeTypes' , _types );
    },
    select({ commit},num){
        commit('select',num)
    },
    toggle( { commit },s ){
		commit( 'toggle',s );
    },
    selectRatings({ commit }){
        commit('selectRatings');
    }
}

var mutations = {
    [types.GET_RATINGS](state,data){
        state.ratings = data;
    },
    changeTypes(state,_types){
        state.types=_types;
    },
    select(state,num){
        state.onlyContent = true;
        if(good.state.foodShow==true){     
            state.ratings=good.state.foodratings;
        };
        state.selectType = num;
        state.selectRatings = state.ratings.filter( rating => {
			if( state.selectType === 2 ){
				return true;
			}else{
				return rating.rateType == state.selectType;
			}
		});
    },
    toggle( state ,s){
        console.log(state.onlyContent);
        if(s==1){      
            state.ratings=good.state.foodratings;
        };    
		state.selectRatings = state.ratings.filter( rating => {
			if( state.onlyContent && !rating.text ){
				return false;
			}else if( state.selectType === 2 ){
				return true;
			}else{
				return rating.rateType == state.selectType;
			}
        });
        state.onlyContent = !state.onlyContent;        
    },
    selectRatings(state){
        state.selectRatings=[];
    }
}
export default{
    state,
    getters,
    actions,
    mutations
}






