<template>
  	<div class="ratings" ref="rt">
      <div class="content">
        <div class="score">
            <div class="left">
               <div class="averScore">{{seller.score}}</div>
               <div class="total">综合评分</div>
               <div class="per">高于周边商家{{seller.rankRate}}％</div> 
            </div>
            <div class="right">
                <div>
                  <p class="ss">服务态度</p>
                  <star :score = 'seller.serviceScore' class="star"></star>
                  <span>{{seller.serviceScore}}</span>
                </div>
                <div>
                  <p class="ss">商品评分</p>
                  <star :score = 'seller.foodScore' class="star"></star> 
                  <span>{{seller.foodScore}}</span>                 
                </div>
                <div>
                  <p class="time">送达时间</p><span class="num">{{seller.deliveryTime}}分钟</span>
                </div>
            </div>
            <div class="clear"></div>
        </div>
        <div class="slice"></div>
        <!-- <div class="header">
            <div class="select" >
                <div class="all" >全部 <span ></span>{{ratings.length}}</div>
                <div class="reco" >满意 <span ></span>{{5}}</div>
                <div class="bad" >不满意 <span >{{5}}</span></div>                        
            </div>
            <div class="have">
                <i class="icon iconfont icon-duihao"></i>
                只看有内容的评价
            </div>
        </div> -->
        <selectrating :flag='2'></selectrating>
        <div class='rating' v-for="(rating,i) in (selectRatings.length==0?ratings:selectRatings)" :key="i">
          <div class="headImg">
            <img :src="rating.avatar">
          </div>
          <div class="rateTime">{{rating.rateTime | formatDateTime}}</div>
          <div class="rside">
            <div class="name">{{rating.username}}</div>
            <div>
              <star :score = 'rating.score' :size = "'small'" class="star"></star>
              <div class="time" v-if="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</div>
            </div>
            <div class="text">{{rating.text}}</div>
            <div class="list">
              <i class="icon iconfont icon-damuzhion" v-if="rating.rateType==0"></i>
              <i class="icon iconfont icon-down" v-if="rating.rateType==1"></i>
              <ul>
                <li class="food" v-for="(food,i) in rating.recommend" :key="i">{{food}}</li>
              </ul>
            </div>
          </div>
          <div class="clear"></div>
        </div>
      </div>  
    </div>
</template>

<script>
import { mapGetters ,mapActions} from 'vuex'
import star from '../star/star'
import BScroll from 'better-scroll'
import select from '../ratingselect/ratingselect'
export default {
  components:{
    star,
    'selectrating':select
  },
  computed:{
    ...mapGetters([
      'ratings',
      'seller',
      'selectRatings'
    ])
  },
  filters:{
    formatDateTime(t){
        var theDate = new Date(t);
        var _hour = theDate.getHours();
        var _minute = theDate.getMinutes();
        var _second = theDate.getSeconds();
        var _year = theDate.getFullYear()
        var _month = theDate.getMonth();
        var _date = theDate.getDate();
        if(_hour<10){_hour="0"+_hour }
        if(_minute<10){_minute="0"+_minute }
        if(_second<10){_second="0"+_second  }
        _month = _month + 1;
        if(_month < 10){_month = "0" + _month;}
        if(_date<10){_date="0"+_date  }
        return  _year + "-" + _month + "-" + _date + " " + _hour + ":" + _minute + ":" + _second ;
    }
  },
  watch:{

    selectRatings(){
          this.$nextTick( ()=>{
              if(!this.ratingBScroll){
                this.ratingBScroll = new BScroll( this.$refs.rt , {click:true} );
              }else{
                this.ratingBScroll.refresh();
              } 
          })
      },
      ratings(){
        this.$nextTick( ()=>{
              if(!this.ratingBScroll){
                this.ratingBScroll = new BScroll( this.$refs.rt , {click:true} );
              }else{
                this.ratingBScroll.refresh();
              } 
          })
      }
  },
  methods:{
	  ...mapActions([

    ]),

  },
  created(){
        this.$store.dispatch('getRatings');
  },
}
</script>

<style scoped lang='less'>
  .ratings{
    position: absolute;
    top: 3.5rem;
    bottom:0.94rem;
    width: 100%;
    height:8.9rem;
    overflow: hidden;
		.score{
			background-color: white;
      padding: 0.36rem 0;
			.left{
				padding: 0 0.48rem;
        width:2rem;
        float: left;
				text-align: center;
				border-right: 1px solid  rgba(7, 17, 27,0.1);
				.averScore{
					font-size: 0.48rem;
					color:rgb(255, 153, 0);
					line-height: 0.56rem;
					margin-bottom: 0.12rem;
				}
				.total{
					font-size: 0.24rem;
					color:rgb(7, 17, 27);
          line-height: 0.24rem;
          margin-bottom: 0.16rem;
				}
				.per{
					font-size: 0.2rem;
					color:rgba(7, 17, 27,0.4);
					line-height: 0.2rem;
        }
      }
      .right{
        float:left;
        padding-left: 0.48rem;
        font-size: 0.24rem;
        color: rgb(7, 17, 27);
        div{
          margin-bottom: 0.16rem;
          height:0.36rem;
          line-height: 0.36rem;
        }
        p{
          display: inline-block; 
          vertical-align: top;      
        }
        .star{
          display: inline-block;
          padding: 0 0.1rem;
          width:2rem;
        }
        span{
          color:rgb(255, 153, 0);
        }
        .time{
          margin-right: 0.24rem;
        }
        .num{
          color:rgba(7, 17, 27,0.4);
        }
      }
      .clear{
        clear: both;
      }
    }
    .slice{
      height: 0.36rem;
      width: 100%;
      background-color: rgba(77, 85, 93,0.1); 
      border-top: 1px solid #ccc;
      border-bottom: 1px solid #ccc;
    }
    // .header{
    //     padding: 0.36rem;
    //     padding-bottom: 0.24rem;
    //     background-color: white;
    //     margin-bottom: 0.02rem;
    //     border-bottom: 1px solid #ccc;
    //     .select{
    //         padding-bottom: 0.36rem;
    //         border-bottom: 1px solid #ccc;
    //         div{
    //             display: inline-block;
    //             margin-right: 0.16rem;
    //             padding: 0.16rem 0.24rem;
    //             font-size:0 .24rem;
    //             line-height: 0.32rem;
    //             border-radius: 0.02rem;
    //             span{
    //                 font-size: 0.16rem;
    //             }
    //         }
    //         .all{
    //             background-color: rgb(0, 160, 220);
    //             color:rgb(255, 255, 255);                        
    //         }
    //         .reco{
    //             background-color: rgba(0, 160, 220,0.2);
    //             color:rgb(77, 85, 93);                        
    //         }
    //         .bad{
    //             color:rgb(77, 85, 93);                                                
    //             background-color: rgba(77, 85, 93,0.2);
    //         }
    //     }
    //     .have{
    //         i{
    //             font-size: 0.48rem;
    //         }
    //         margin-top: 0.24rem;
    //         font-size: 0.24rem;
    //         color: rgb(147, 153, 159);
    //         line-height: 0.48rem;
    //     }

    // }
    .rating{
      padding:0.36rem;
      border-bottom: 1px solid rgba(7, 17, 27,0.1);
      position: relative;
      .clear{
            clear: both;
      }
      .rateTime{
        position: absolute;
        right: 0.36rem;
        top:0.36rem;
        font-size: 0.2rem;
        font-weight: 200;
        color:rgb(147, 153, 159);
        line-height: 0.24rem;
      }
      .headImg{
        width: 0.56rem;
        height:0.56rem;
        float: left;
        img{
          height:100%;
          border-radius: 0.28rem;
        }
      }
      .rside{
        float: left;
        width: 5.8rem;
        margin-left: 0.24rem;
        .name{
          font-size: 0.2rem;
          color:rgb(7, 17, 27);
          line-height: 0.24rem;
          margin-bottom: 0.08rem;
        }
        .star,.time{
          display: inline-block;
        }
        .time{
          font-size: 0.2rem;
          font-weight: 200;
          color:rgb(147, 153, 159);
          line-height: 0.24rem;
          margin-bottom: 0.12rem;
          margin-left: 0.12rem;
        }
        .text{
          font-size: 0.24rem;
          color:rgb(7, 17, 27);
          line-height: 0.36rem;
          margin-bottom: 0.16rem;
        }
        .list{
          i{
            font-size: 0.24rem;
            line-height: 0.32rem;
          }
          .icon-damuzhion{
            color:rgb(0, 160, 220);
          }
          .icon-down{
            color: rgb(183, 187, 191);
          }
          ul{
            float: right;
            width: 5.4rem;
          }
          .food{
            display: inline-block;
            padding: 0 0.12rem;
            border:1px solid rgba(7, 17, 27,0.1);
            border-radius: 0.02rem;
            background-color: rgb(255, 255, 255);
            margin-left: 0.16rem;
            font-size: 0.18rem;
            color: rgb(147, 153, 159);
            line-height: 0.32rem;
            margin-bottom: 0.1rem;
          }
        }
        
      }
    }
  }
</style>



