<template>
    <div class="select">
        <div class="header">
            <div class="select" >
                <div class="all" @click="select(2)">{{types[2]}} <span >{{totalCount}}</span></div>
                <div class="reco" @click="select(0)">{{types[0]}} <span >{{goodCount}}</span></div>
                <div class="bad" @click="select(1)">{{types[1]}} <span >{{badCount}}</span></div>                        
            </div>
            <div class="have" @click = 'toggle' :class="{'current' : show == true}">
                <i class="icon iconfont icon-duihao"></i>
                <span>只看有内容的评价</span> 
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters ,mapActions} from 'vuex'
export default {
    props:[
        'flag'
    ],
    data(){
        return {
            show:false
        }
    },
    created(){
        this.$store.dispatch('changeTypes',['满意','不满意','全部']);
    },
    computed:{
        ...mapGetters([
            'ratings',
            'types',
            'foodratings',
            'onlyContent'
        ]),
        totalCount(){
            if(this.flag ==1){
                return this.foodratings.length
            }else{
                return this.ratings.length
            }
        },
        goodCount(){
            if(this.flag ==1 ){
                return this.foodratings.filter(rating =>{
                    return rating.rateType===0;
                }).length;
            }else{
                return this.ratings.filter(rating =>{
                    return rating.rateType===0;
                }).length;
            }
            
        },
        badCount(){
            if (this.flag ==1) {
                return this.foodratings.length-this.goodCount;
            }else{
                return this.ratings.length-this.goodCount;
            } 
        }
    },
    methods:{
        select(num){
            this.show=false;
            this.$store.dispatch('select',num)
        },
        toggle(){
            this.show = !this.show;
            this.$store.dispatch('toggle',this.flag);
        }
    }
}
</script>
<style scoped lang='less'>
    .header{
        padding: 0.36rem;
        padding-bottom: 0.24rem;
        background-color: white;
        margin-bottom: 0.02rem;
        border-bottom: 1px solid #ccc;
        .select{
            padding-bottom: 0.36rem;
            border-bottom: 1px solid #ccc;
            div{
                display: inline-block;
                margin-right: 0.16rem;
                padding: 0.16rem 0.24rem;
                font-size:0 .24rem;
                line-height: 0.32rem;
                border-radius: 0.02rem;
                span{
                    font-size: 0.16rem;
                }
            }
            .all{
                background-color: rgb(0, 160, 220);
                color:rgb(255, 255, 255);                        
            }
            .reco{
                background-color: rgba(0, 160, 220,0.2);
                color:rgb(77, 85, 93);                        
            }
            .bad{
                color:rgb(77, 85, 93);                                                
                background-color: rgba(77, 85, 93,0.2);
            }
        }
        .have{
            &.current{
                color: rgb(0, 160, 220);
            }
            i{
                font-size: 0.48rem;  
            }
            span{
                font-size: 0.24rem;  
                color: rgb(147, 153, 159); 
                vertical-align: top;                     
            }
            margin-top: 0.24rem;
            color: rgb(147, 153, 159);
            line-height: 0.48rem;
        }

    }
</style>



